import { MessageBox, Message } from 'element-ui';

export default {
  errorMessage(text) {
    MessageBox.alert(text, '', {
      type: 'error',
      confirmButtonText: '',
    });
  },
  successMessage(text, callback) {
    MessageBox.alert(text, '', {
      type: 'success',
      confirmButtonText: '',
    }).then(() => {
      callback();
    }).catch(() => {
    });
  },
  confirmMessage(text, callback) {
    MessageBox.confirm(text, '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning',
    }).then(() => {
      callback();
    }).catch(() => {
    });
  },
  success(text) {
    Message({
      showClose: true,
      message: text,
      type: 'success',
    });
  },
};



